/* Main Layout */
.Home-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Glassmorphism Card */
.glass-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 40px;
  text-align: center;
  color: white;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  transition: transform 0.3s ease;
}

/* 3D Hover Effect for Cards on Home Page */
.Home-card:hover {
  transform: translateY(-10px);
  background: rgba(255, 255, 255, 0.2);
}

/* Glass Buttons */
.glass-btn {
  display: inline-block;
  margin: 10px;
  padding: 12px 30px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.4);
  color: white;
  text-decoration: none;
  border-radius: 30px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.glass-btn:hover {
  background: rgba(255, 255, 255, 0.4);
  transform: scale(1.05);
}

/* Form Inputs */
.glass-input {
  display: block;
  width: 100%;
  margin: 15px 0;
  padding: 12px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  color: white;
  outline: none;
}

.glass-input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

/* Notification Badge */
.notif-badge {
  position: absolute;
  top: -10px;
  right: -10px;
  background: #ff4757;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  box-shadow: 0 4px 10px rgba(0,0,0,0.3);
  border: 2px solid white;
}

/* Success Modal Overlay */
.success-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(8px);
}

.success-popup {
  animation: popIn 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

@keyframes popIn {
  0% { transform: scale(0.5); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}
/* New Grid Layout for Skills Marketplace */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 25px;
  padding-bottom: 50px;
}

/* Specific Skill Card Styling */
.skill-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 25px !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
}

/* Neat Category Tags */
.category-tag {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(130, 204, 221, 0.2);
  color: #82ccdd;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
  border: 1px solid rgba(130, 204, 221, 0.3);
}

.skill-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px 0 rgba(31, 38, 135, 0.5);
  background: rgba(255, 255, 255, 0.15);
}
.search-bar {
  max-width: 600px;
  font-size: 1.1rem;
  padding: 15px 25px !important;
  border-radius: 40px !important;
  background: rgba(255, 255, 255, 0.15) !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transition: 0.3s ease;
}

.search-bar:focus {
  background: rgba(255, 255, 255, 0.25) !important;
  transform: scale(1.02);
  border-color: #82ccdd !important;
}
.featured-item {
  padding: 20px 40px !important;
  min-width: 150px;
  border-radius: 50px !important;
  background: rgba(255, 255, 255, 0.05) !important;
  transition: 0.3s;
}

.featured-item:hover {
  background: rgba(255, 255, 255, 0.2) !important;
  transform: scale(1.1);
  border-color: #82ccdd !important;
}
/* Glass Navbar Styling */
.glass-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 5%; /* Use percentage padding to keep buttons away from edges */
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  position: fixed;
  top: 0;
  left: 0;
  right: 0; /* Ensures it stretches but stays within bounds */
  z-index: 1000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box; /* Crucial to prevent horizontal overflow */
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 20px; /* Even spacing between all items */
}

.nav-btn {
  padding: 8px 18px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.15) !important;
  border: 1px solid rgba(255, 255, 255, 0.3);
  text-decoration: none;
  color: white !important;
  font-weight: 500;
  transition: all 0.3s ease;
}

.register-btn {
  background: #00d2ff !important; /* Brighter color to make it pop */
  color: #1a1a1a !important;
  box-shadow: 0 4px 15px rgba(0, 210, 255, 0.3);
}

.nav-btn:hover {
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.25) !important;
}
/* Skill Swap Logo Styling */
.nav-logo a {
  font-size: 1.6rem;
  font-weight: 800;
  /* Applying a vibrant gradient to the logo text */
  background: linear-gradient(45deg, #00d2ff, #3a7bd5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-decoration: none;
  letter-spacing: 1px;
}

/* Marketplace and Profile Link Styling */
.nav-links a {
  color: rgba(255, 255, 255, 0.85); /* Clean off-white for readability */
  text-decoration: none;
  margin-left: 25px;
  font-weight: 600;
  font-size: 1rem;
  transition: all 0.3s ease;
  position: relative;
}

/* Hover Effect for Nav Links */
.nav-links a:hover {
  color: #00d2ff; /* Matches the logo gradient on hover */
  transform: translateY(-2px);
}

/* Bottom line animation on hover */
.nav-links a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: #00d2ff;
  transition: width 0.3s ease;
}

.nav-links a:hover::after {
  width: 100%;
}
/* Skill Swap Logo - Vibrant Gradient */
.nav-logo a {
  font-size: 1.6rem;
  font-weight: 800;
  background: linear-gradient(45deg, #00d2ff, #3a7bd5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-decoration: none;
}

/* Base style for Marketplace/Profile */
.nav-links a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  margin-left: 20px;
  font-weight: 500;
  transition: 0.3s;
}

/* The color when the page is ACTIVE */
.active-link {
  color: #00d2ff !important; /* Brighter cyan color */
  font-weight: 700 !important;
  text-shadow: 0 0 10px rgba(0, 210, 255, 0.5);
}

/* Hover state */
.nav-links a:hover {
  color: #ffffff;
}
/* Enhanced Active Link with Animated Dot */
.active-link {
  color: #00d2ff !important;
  font-weight: 700 !important;
  position: relative; /* Needed to place the dot */
  text-shadow: 0 0 15px rgba(0, 210, 255, 0.6);
}

/* The Animated Dot */
.active-link::after {
  content: 'â€¢';
  position: absolute;
  bottom: -12px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.2rem;
  color: #00d2ff;
  animation: bounceDot 0.6s ease-out;
