/* Bounce Animation for the Dot */
@keyframes bounceDot {
  0% { transform: translate(-50%, 5px); opacity: 0; }
  100% { transform: translate(-50%, 0); opacity: 1; }
}
.nav-logo a {
  display: flex;
  align-items: center;
  gap: 12px;
  text-decoration: none;
}

.navbar-logo-icon {
  height: 40px; /* Adjust this to make the logo larger or smaller */
  width: auto;
  border-radius: 8px; /* Optional: gives the logo slightly rounded corners */
